import React, { Component } from 'react';

import { Nav, Container } from 'react-bootstrap';

import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { getEvents, extractLocations } from './api';
import { WarningAlert } from './Alert';

import logo from './img/logo.png';
import './App.css';
import './nprogress.css';

class App extends Component {
    state = {
        events: [],
        locations: [],
        currentLocation: 'all',
        numberOfEvents: 32,
    };

    componentDidMount() {
        this.mounted = true;
        getEvents().then((events) => {
            if (this.mounted) {
                this.setState({
                    events: events.slice(0, this.state.numberOfEvents),
                    locations: extractLocations(events),
                });
            }
        });
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    updateEvents = (location, eventCount) => {
        this.mounted = true;
        getEvents().then((events) => {
            const locationEvents =
                location === 'all' && eventCount === 0
                    ? events
                    : location !== 'all' && eventCount === 0
                    ? events.filter((event) => event.location === location)
                    : events.slice(0, eventCount);
            if (this.mounted) {
                this.setState({
                    events: locationEvents,
                    numberOfEvents: eventCount,
                });
            }
        });
    };

    render() {
        return (
            <Container className="App">
                <Nav className="navbar justify-content-center" bg="black">
                    <Nav.Link href="#home">
                        <img src={logo} className="nav-logo" alt="The DevUps logo" />
                    </Nav.Link>
                    {!navigator.onLine ? (
                        <WarningAlert text="No internet connection! Please connect to the internet for an updated list of events." />
                    ) : (
                        <WarningAlert text="" />
                    )}
                </Nav>

                <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} />
                <NumberOfEvents numberOfEvents={this.state.numberOfEvents} updateEvents={this.updateEvents} />
                <EventList events={this.state.events} />
            </Container>
        );
    }
}

export default App;
